    @charset "utf-8";
    // 引用基础框架样式库
    @import "lib/all";
    $header-color:#00417d;
    $base-color:#fff;
    .fd-left {
        float: left;
    }
    
    .fd-right {
        float: right;
    }
    
    .fd-clear {
        clear: both;
    }
    /* icon */
    
    .m-icon,
    .fp-controlArrow {
        background-image: url(../images/m-icon.png);
        background-repeat: no-repeat;
        vertical-align: middle;
        display: inline-block;
    }
    
    .tm {
        width: 488px;
        height: 37px;
        background-position: 0 0;
        display: block;
        margin: auto;
    }
    
    .all {
        display: block;
        width: 138px;
        height: 138px;
        background-position: 0 -37px;
        margin: auto;
    }
    
    .auto {
        display: block;
        width: 138px;
        height: 138px;
        background-position: 0 -175px;
        margin: auto;
    }
    
    .mach {
        display: block;
        width: 138px;
        height: 138px;
        background-position: 0 -313px;
        margin: auto;
    }
    /* tm */
    
    .tm-in {
        @extend %clearfix;
        padding: 50px 0;
        li {
            float: left;
            width: 33.33%;
            p {
                font-size: 28px;
                text-align: center;
                margin: 20px 0;
                &.in-all {
                    color: #00c9dd;
                }
                &.in-auto {
                    color: #00a3fe;
                }
                &.in-mach {
                    color: #5f52a1;
                }
            }
            .in-intro {
                font-size: 14px;
                color: #585858;
                line-height: 26px;
                padding: 0 6%;
            }
        }
    }
    /* nav */
    
    body {
        font-family: 'Microsoft YaHei', sans-serif;
    }
    
    #fp-nav ul li a span {
        width: 12px;
        height: 12px;
        margin: -6px 0 0 -6px;
        background-color: #0b2b5c;
    }
    
    #fp-nav ul li:hover a span {
        width: 12px;
        height: 12px;
        margin: -6px 0 0 -6px;
        background-color: #37b0fc;
    }
    
    #fp-nav ul li a.active span {
        background-color: #37b0fc;
    }
    
    .fp-controlArrow {
        margin-top: -22px;
    }
    
    .fp-controlArrow.fp-next {
        border: 0 none;
        background-position: 0 -451px;
        width: 24px;
        height: 44px;
        right: 50px;
    }
    
    .fp-controlArrow.fp-prev {
        border: 0 none;
        background-position: 0 -495px;
        width: 24px;
        height: 44px;
        left: 50px;
    }
    
    .section {
        background-image: url(../images/submit_bg.png);
        background-repeat: no-repeat;
        background-position: bottom center;
    }
    
    .meeting-nav {
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        z-index: 9999;
        .logo {
            float: left;
            margin: 2% 0 0 3%;
        }
        .menu {
            float: right;
            width: 60%;
            margin-top: 3%;
            li {
                float: left;
                list-style: none;
                margin-right: 4%;
                &.active {
                    a {
                        color: #37b0fc;
                    }
                }
                a {
                    font-size: 24px;
                    color: $base-color;
                }
            }
        }
    }
    /* 首页 */
    
    .m-index {
        .i-tm {
            text-align: center;
            margin-bottom: -130px;
            margin-top: 40px;
        }
        .index-area {
            text-align: center;
            .cn-title {
                font-size: 90px;
                color: #fff;
                width: 752px;
                text-align: justify;
                text-align-last: justify;
                margin: auto;
            }
            .en-title {
                font-size: 34px;
                color: #fff;
                width: 752px;
                text-align: justify;
                font-family: 'Arial';
                margin: auto;
                &:after {
                    display: inline-block;
                    overflow: hidden;
                    width: 100%;
                    height: 0;
                    content: '';
                    vertical-align: top;
                }
            }
            .m-btn {
                display: inline-block;
                width: 280px;
                height: 50px;
                line-height: 50px;
                text-align: center;
                background-color: #f39b00;
                color: #fff;
                font-size: 28px;
                border-radius: 25px;
                transition: .2s;
                &:hover{
                    background-color: #f1af3a;
                }
            }
            .m-time,
            .e-time {
                font-size: 22px;
                color: #fff;
            }
            .m-time {
                width: 400px;
                margin: 70px auto 10px;
                overflow: hidden;
                position: relative;
            }
            .m-time span {
                float: left;
                text-align: center;
                //            width: 33.3%;
            }
            .m-time .border {
                border-bottom: 1px solid #fff;
                height: 18px;
                width: 50px;
            }
            .m-name {
                padding: 0 20px;
            }
        }
    }
    /* 峰会嘉宾 */
    
    .guest .guestList {
        float: left;
        width: 160px;
        text-align: center;
    }
    
    .guest .guestName {
        font-size: 20px;
        color: #fff;
    }
    
    .guest .specilaName {
        width: 190px;
    }
    
    .guest .specilaIntro {
        width: auto;
        white-space: nowrap;
    }
    
    .guest .gusdtTitle {
        font-size: 20px;
        color: #fff;
    }
    
    .guestImgBox {
        display: inline-block;
        cursor: pointer;
        img {
            width: 156px;
        }
    }
    
    .t-meet {
        font-size: 36px;
        color: #37b0fc;
        text-align: center;
        margin-bottom: 2%;
    }
    
    .s-meet {
        color: #ffffff;
        font-size: 28px;
        margin-top: -2%;
    }
    
    .meet-guest {
        .t-meet {
            margin-bottom: 2%;
        }
        .guest {
            @extend %clearfix;
            width: 80%;
            margin: auto;
            li {
                width: 25%;
            }
        }
    }
    /* 峰会议程 */
    
    .schedule {
        .m-schedule {
            color: #fff;
            font-size: 20px;
            border-top: 1px solid #357fb7;
            border-bottom: 1px solid #357fb7;
            border: 1px solid #46a4dc;
            margin: auto;
            th,
            td {
                text-align: center;
                padding: 5px 0;
                border: 1px solid #46a4dc;
            }
            td {}
            .row1 {
                width: 10%;
            }
            .row3 {
                width: 16%;
            }
            .row4{
                width: 7%;
            }
            .row5{
                width: 30%;
            }
        }
    }
    /* meet-partner */
    
    .ad {
        display: inline-block;
        float: left;
        margin: 0 25px 40px 0;
        vertical-align: middle;
        border: 1px solid #d4d4d4;
        background-color: #fff;
    }
    
    .ad img {
        width: 157px;
        height: 52px;
        width/*\**/
        : auto\9;
        *width: 157px;
    }
    
    .ad-no {
        margin-right: 0;
    }
    
    .meet-partner {
        .logo-area {
            width: 920px;
            margin: auto;
            overflow: hidden;
            padding-left: 25px;
        }
    }
    /* 往届回顾 */
    
    .past {
        .past-area {
            width: 972px;
            margin: auto;
        }
        .r-pic {
            position: relative;
            overflow: hidden;
            width: 461px;
            height: 495px;
            float: left;
            img {
                -webkit-transition: all linear .2s;
                transition: all linear .2s;
            }
            &:hover {
                img {
                    -webkit-transform: scale(1.1);
                    transform: scale(1.1);
                }
            }
        }
        .r-pic .s-pic {
            position: absolute;
            bottom: 0;
            display: block;
            width: 100%;
            padding: 15px 0;
            font-size: 18px;
            font-weight: 600;
            text-align: center;
            color: #fff;
            background-color: #48b2f8;
        }
        .m50 {
            margin-left: 50px;
        }
    }
    
    /* dialog */
    
    button::-moz-focus-inner {
        border: 0;
        padding: 0;
        margin: 0;
    }
    
    .easyDialog_wrapper {
        width: 750px;
        color: #444;
        display: none;
        font-family: "Microsoft yahei", Arial;
    }
    
    .easyDialog_wrapper .easyDialog_content {
        background: #fff;
    }
    
    .easyDialog_wrapper .easyDialog_title {
        overflow: hidden;
        color: #666;
        width: 90%;
        margin: auto;
        font-size: 14px;
        padding-top: 40px;
        background: #fff;
    }
    
    .easyDialog_wrapper .close_btn {
        font-family: arial;
        font-size: 18px;
        _font-size: 12px;
        font-weight: 700;
        color: #999;
        text-decoration: none;
        float: right;
    }
    
    .easyDialog_wrapper .close_btn:hover {
        color: #333;
    }
    
    .easyDialog_wrapper .easyDialog_text {
        padding: 0 0 25px 0;
        width: 90%;
        margin: auto;
        font-size: 13px;
        line-height: 22px;
    }
    
    .easyDialog_wrapper .easyDialog_footer {
        padding: 0 10px;
        *zoom: 1;
    }
    
    .easyDialog_wrapper .easyDialog_footer:after {
        content: '';
        display: block;
        height: 0;
        overflow: hidden;
        visibility: hidden;
        clear: both;
    }
    
    .easyDialog_wrapper .btn_highlight,
    .easyDialog_wrapper .btn_normal {
        border: 1px solid;
        border-radius: 2px;
        cursor: pointer;
        font-family: "Microsoft yahei", Arial;
        float: right;
        font-size: 12px;
        padding: 0 12px;
        height: 24px;
        line-height: 24px;
        margin-bottom: 10px;
    }
    
    .easyDialog_wrapper .btn_highlight {
        background: #4787ed;
        background: -webkit-gradient(linear, center bottom, center top, from(#4787ed), to(#4d90fe));
        background: -moz-linear-gradient(90deg, #4787ed, #4d90fe);
        border-color: #3079ed;
        color: #fff;
    }
    
    .easyDialog_wrapper .btn_normal {
        margin-left: 10px;
        border-color: #c3c3c3;
        background: #ececec;
        color: #333;
        background: -webkit-gradient(linear, center bottom, center top, from(#ececec), to(#f4f4f4));
        background: -moz-linear-gradient(90deg, #ececec, #f4f4f4);
    }
    
    @-webkit-keyframes bounceIn {
        from,
        20%,
        40%,
        60%,
        80%,
        to {
            -webkit-animation-timing-function: cubic-bezier(0.215, 0.610, 0.355, 1.000);
            animation-timing-function: cubic-bezier(0.215, 0.610, 0.355, 1.000);
        }
        0% {
            opacity: 0;
            -webkit-transform: scale3d(.3, .3, .3);
            transform: scale3d(.3, .3, .3);
        }
        20% {
            -webkit-transform: scale3d(1.1, 1.1, 1.1);
            transform: scale3d(1.1, 1.1, 1.1);
        }
        40% {
            -webkit-transform: scale3d(.9, .9, .9);
            transform: scale3d(.9, .9, .9);
        }
        60% {
            opacity: 1;
            -webkit-transform: scale3d(1.03, 1.03, 1.03);
            transform: scale3d(1.03, 1.03, 1.03);
        }
        80% {
            -webkit-transform: scale3d(.97, .97, .97);
            transform: scale3d(.97, .97, .97);
        }
        to {
            opacity: 1;
            -webkit-transform: scale3d(1, 1, 1);
            transform: scale3d(1, 1, 1);
        }
    }
    
    @keyframes bounceIn {
        from,
        20%,
        40%,
        60%,
        80%,
        to {
            -webkit-animation-timing-function: cubic-bezier(0.215, 0.610, 0.355, 1.000);
            animation-timing-function: cubic-bezier(0.215, 0.610, 0.355, 1.000);
        }
        0% {
            opacity: 0;
            -webkit-transform: scale3d(.3, .3, .3);
            transform: scale3d(.3, .3, .3);
        }
        20% {
            -webkit-transform: scale3d(1.1, 1.1, 1.1);
            transform: scale3d(1.1, 1.1, 1.1);
        }
        40% {
            -webkit-transform: scale3d(.9, .9, .9);
            transform: scale3d(.9, .9, .9);
        }
        60% {
            opacity: 1;
            -webkit-transform: scale3d(1.03, 1.03, 1.03);
            transform: scale3d(1.03, 1.03, 1.03);
        }
        80% {
            -webkit-transform: scale3d(.97, .97, .97);
            transform: scale3d(.97, .97, .97);
        }
        to {
            opacity: 1;
            -webkit-transform: scale3d(1, 1, 1);
            transform: scale3d(1, 1, 1);
        }
    }
    
    @-webkit-keyframes bounceOut {
        20% {
            -webkit-transform: scale3d(.9, .9, .9);
            transform: scale3d(.9, .9, .9);
        }
        50%,
        55% {
            opacity: 1;
            -webkit-transform: scale3d(1.1, 1.1, 1.1);
            transform: scale3d(1.1, 1.1, 1.1);
        }
        to {
            opacity: 0;
            -webkit-transform: scale3d(.3, .3, .3);
            transform: scale3d(.3, .3, .3);
        }
    }
    
    @keyframes bounceOut {
        20% {
            -webkit-transform: scale3d(.9, .9, .9);
            transform: scale3d(.9, .9, .9);
        }
        50%,
        55% {
            opacity: 1;
            -webkit-transform: scale3d(1.1, 1.1, 1.1);
            transform: scale3d(1.1, 1.1, 1.1);
        }
        to {
            opacity: 0;
            -webkit-transform: scale3d(.3, .3, .3);
            transform: scale3d(.3, .3, .3);
        }
    }
    
    .bounceOut {
        -webkit-animation-name: bounceOut;
        animation-name: bounceOut;
        -webkit-animation-duration: .75s;
        animation-duration: .75s;
        -webkit-animation-duration: 1s;
        animation-duration: 1s;
        -webkit-animation-fill-mode: both;
        animation-fill-mode: both;
    }
    
    @-webkit-keyframes bounceOutDown {
        20% {
            -webkit-transform: translate3d(0, 10px, 0);
            transform: translate3d(0, 10px, 0);
        }
        40%,
        45% {
            opacity: 1;
            -webkit-transform: translate3d(0, -20px, 0);
            transform: translate3d(0, -20px, 0);
        }
        to {
            opacity: 0;
            -webkit-transform: translate3d(0, 2000px, 0);
            transform: translate3d(0, 2000px, 0);
        }
    }
    
    .bounceIn {
        -webkit-animation-name: bounceIn;
        animation-name: bounceIn;
        -webkit-animation-duration: .75s;
        animation-duration: .75s;
        -webkit-animation-duration: 1s;
        animation-duration: 1s;
        -webkit-animation-fill-mode: both;
        animation-fill-mode: both;
    }
    /* 弹窗 */
    
    .guest-box {
        padding: 40px 30px 40px 30px;
        background-color: #fff;
    }
    
    .guest-box img {
        width: 118px;
        height: 118px;
        border-radius: 50%;
        width/*\**/
        : auto\9;
        *width: auto;
    }
    
    .guest-box .guest-title {
        margin: 19px 0 0 20px;
    }
    
    .guest-box .guest-name {
        margin-right: 5px;
        font-size: 30px;
        color: #111;
    }
    
    .guest-box .guest-pri {
        font-size: 18px;
        color: #999;
    }
    
    .guest-box .guset-match {
        font-size: 18px;
        color: #00509d;
    }
    
    .easyDialog_wrapper .close_btn {
        position: absolute;
        top: 15px;
        right: 20px;
        display: inline-block;
        width: 29px;
        height: 29px;
        text-decoration: none;
        background: url(//pic.c-ctrip.com/corp_niv/meeting/images/x.png);
        text-indent: -99999px;
    }
    
    .guest-box .guest-intro {
        margin-top: 20px;
        padding: 30px 0;
        font-size: 18px;
        text-align: justify;
        color: #999;
        border-top: 1px solid #dedede;
        border-bottom: 1px solid #dedede;
    }
    
    .guest-box .guest-word {
        margin-top: 40px;
    }
    
    .schedule .phone-order {
        display: none;
    }
    @media screen and (max-width: 640px) {
        #fp-nav ul li, .fp-slidesNav ul li{
            margin: 7px 0;
        }
        #fp-nav.right{
            right: 0;
        }
        .meeting-nav {
            background-color: rgba(0,0,0,0.8);
        }
        .guestImgBox {
            display: inline-block;
            cursor: pointer;
            img {
                width: 50%;
            }
        }
        .guest .guestName,
        .guest .gusdtTitle {
            font-size: 12px;
        }
        .section{
            background: #000c40;
        }
        .m-index .index-area .cn-title{
            font-size: 26px;
            width: 300px;
        }
        .m-index .index-area .en-title{
            font-size: 14px;
            width: 300px;
        }
        .meeting-nav .logo{
            display: none;
        }
        .meeting-nav .menu{
            width: 100%;
            float: none;
            display: -webkit-box;
            display: -ms-flexbox;
            display: flex;
            -webkit-box-pack:justify;
            -ms-flex-pack:justify;
            justify-content:space-between;
            margin-top: 0;
        }
        .meeting-nav .menu li{
            margin-right: 0;
            padding: 10px 0;
        }
        .easyDialog_wrapper{
            width: 300px;
        }
        .tm{
            width: 244px;
            height: 18px;
            background-size: cover;
        }
        .easyDialog_wrapper .close_btn {
            background: none;
            text-indent: 0;
            font-size: 22px;
            font-weight: normal;
            font-family: serif;
            right: -6px;
            top: -1px;
        }
        .tm-in li{
            float: none;
            width: 100%;
            i{
                display: none;
            }
        }
        .tm-in li .in-intro{
            font-size: 12px;
        }
        .easyDialog_wrapper .easyDialog_title{
            padding-top: 10px;
        }
        .tm-in{
            padding: 0;
        }
        .tm-in li p{
            font-size: 18px;
