        }
        .easyDialog_wrapper .easyDialog_text{
            padding-bottom: 10px;
        }
        .guest-box .guest-intro{
            font-size: 12px;
        }
        .guest-box img{
            width: 59px;
            height: 59px;
        }
        .guest-box .guest-title{
            margin: 5px 0 0 10px;
        }
        .guest-box .guest-name{
            font-size: 18px;
        }
        .guest-box .guest-pri{
            font-size: 12px;
        }
        .guest-box .guset-match{
            font-size: 12px;
        }
        .guest-box .guest-intro{
            margin-top: 10px;
            padding: 10px 0;
        }
        .guest-box .guest-word{
            margin-top: 10px;
        }
        .meet-guest .guest{
            width: 90%;
        }
        .meet-guest .guest li{
            width: 33.33%;
        }
        .t-meet{
            font-size: 18px;
        }
        .meeting-nav .menu li a{
            font-size: 12px;
        }
        .schedule .m-schedule{
            width: 90%;
            font-size: 12px;
        }
        .schedule .m-schedule th, .schedule .m-schedule td{
            padding: 0;
        }
        .s-meet{
            font-size: 12px;
        }
        .past .r-pic .s-pic{
            font-size: 12px;
        }
        .fp-controlArrow.fp-next{
            right: 15px;
        }
    }